a {
  text-decoration: none !important; }

.App {
  justify-content: center;
  display: flex; }

.App_Cover {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column; }

.Cover {
  display: flex;
  width: 90%;
  margin: auto;
  justify-content: center;
  flex-direction: column; }

.All_Movies {
  flex: 1;
  width: 95%;
  max-width: 1250px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; }

.Single_Movie {
  display: flex;
  flex-direction: column;
  width: 240px;
  margin: 10px 5px;
  text-align: center; }

.Single_Movie_Name {
  width: 100%;
  align-content: center;
  display: flex;
  height: 50px;
  text-align: center; }

.Single_Movie_Name div {
  display: flex;
  margin: auto; }

.Single_Movie_Bottom {
  display: flex;
  justify-content: space-evenly; }

.Single_Movie_Edit {
  display: flex;
  flex-direction: row;
  width: 95%;
  margin: 10px auto;
  justify-content: center; }

.Single_Movie_Edit img {
  width: 300px;
  height: 425px; }

.Single_Movie_Description {
  margin-left: 20px;
  max-width: 700px;
  text-align: justify; }

.Edit_Movie {
  width: 95%;
  max-width: 1020px;
  margin: auto; }

.Header_Page {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70px; }

.Header_Page h2 {
  margin: 0; }

.Header_Page .Header_Page_Cover {
  width: 95%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between; }

.Footer_Page {
  display: flex;
  margin: auto 0 0 0;
  align-self: flex-end;
  justify-self: end;
  height: 50px;
  width: 100%; }
